<template>
  <div id="manage-box">
    <SimpMain>
      <SliderMenu :menus="menus" :activeName="activeName" :openNames="openNames"></SliderMenu>
    </SimpMain>
  </div>
</template>

<script>
/* 导入内部组件 */
import SimpMain from '../../components/SimpMain.vue'
import SliderMenu from '../../components/SliderMenu.vue'

export default {
  name: 'manage',
  data () {
    return {
      activeName: '1-1',
      openNames: ['1'],
      menus: [
        {
          id: '1',
          title: '系统管理',
          icon: 'ios-paper',
          child: [
            {
              id: '1-1',
              title: '用户管理',
              url: '/main/systemManage/userManage',
              meta: {
                id: 19
              }
            },
            {
              id: '1-2',
              title: '角色管理',
              url: '/main/systemManage/roleManage',
              meta: {
                id: 20
              }
            },
            {
              id: '1-4',
              title: '备份管理',
              url: '/main/systemManage/userManage',
              meta: {
                id: 23
              }
            },
            {
              id: '1-5',
              title: '版本管理',
              url: '/main/systemManage/versionManage',
              meta: {
                id: 21
              }
            },
            {
              id: '1-3',
              title: '回收站',
              url: '/main/systemManage/recycleManage',
              meta: {
                id: 22
              }
            },
            {
              id: '1-6',
              title: '字典维护',
              url: '/main/systemManage/userManage',
              meta: {
                id: 24
              }
            }
          ]
        }
      ]
    }
  },
  components: {
    SimpMain,
    SliderMenu
  },
  mounted () {
    let arr = []
    if (this.$store.getters.getloginInfor() !== null) {
      for (let item of this.$store.getters.getloginInfor().resource) {
        arr.push(item.id)
      }
    }
    this.menus[0].child = this.menus[0].child.filter(wes => arr.indexOf(wes.meta.id) !== -1)
  },
  methods: {}
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #manage-box{
    width:100%;
    min-width: 1200px;
    min-height:100%;
    background: #efeeee;
    background-size: 100% 100%;
    display:flex;
    flex-direction:column;
    padding: 0;
    #content{
      text-align: center;
      height:calc(100vh - 117px);
      position: relative;
      .sliderMenu_box{
        height:100%;
        background:#fff;
        width:200px;
        overflow-y:auto;
        overflow-x: hidden;
        position: absolute;
        box-shadow: 0 0 8px 4px rgba(0,0,0,0.1);
        z-index: 456;
      }
      .right_box{
        position: absolute;
        left:200px;
        width:calc(100vw - 200px);
        .navbar_box{
          position: absolute;
          z-index: 100;
          width: 100%;
        }
        .routerView_box{
          width: 100%;
          padding: 10px;
          height:calc(100vh - 148px);
          position: absolute;
          top:31px;
          z-index: 200;
          background: #eee;
          overflow-x: hidden;
          overflow-y: auto;
          box-shadow: 0 0 6px 4px rgba(0,0,0,0.1);
        }
        z-index: 300;
      }
    }

    /*底部内容的样式*/
    #footer{
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 0;
      height: 35px;
      background:#fff;
      line-height: 35px;
      overflow: hidden;
      p{
        text-align: center;
        font-size: 12px;
        color:#999;
      }
    }
  }
</style>
